      let route = this.$router
      route.push({ path: '/advanced/DriverList/add' })
    },
    isDisabled: function () {
      this.isEmployee = this.$cookie.get('isEmployee')
      return !this.$cookie.get('isEmployee')
    },
    editItem (id) {
      this.$router.push({ path: '/advanced/DriverList/edit/' + id })
    },
    deleteItem (mobno) {
      let hostname = this.$hostName
      var r = confirm('Do you want to Delete?')
      if (r === true) {
        axios.post(hostname + '/api/driver/deleteDriver', { mobno }).then(res => {
          if (res.data.affectedRows > 0) {
            window.location.reload()
          } else alert("Driver Doesn't Exist")
        })
      }
    }
