  {
    filteredCustomers: function () {
      let self = this
      if (this.items) {
        return this.items.filter(function (cust) {
          return cust.driver_id.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 ||
        cust.driver_name.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 ||
        cust.driver_phone.toLowerCase().indexOf(self.search.toLowerCase()) >= 0
        })
      }
