<template>
<div class="Admin-view">
<!-- Content Header (Page header) -->
<section class="content-header">
<div class="row">
<div class="col-md-9">
<h2>
Assign Driver
</h2></div>
<ol class="breadcrumb">
<li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
<li class="active">Assign Driver</li>
</ol>
</div>
</section>
<!-- /.content-header -->

<!-- Content Body-->

<div class="col-md-12">
<div class="card">
<div class="card-header">
<div class="row">
<div class="col-sm-6"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
</div>
</div>
<!-- /.card-header -->
<div class="card-body p-0">
<table class="table table-condensed">
<tr>
<th style="width: 11%">BookingID</th>
<th style="width: 10%">Book Date</th>
<th style="width: 11%">Book Time</th>
<th style="width: 14%">User Name</th>
<th style="width: 12%">User Mobile</th>
<th style="width: 15%">Driver</th>
<th style="width: 19%;">Action</th>
</tr>
<tbody>
<tr v-for="(item,index) in filteredCustomers">
<td>{{ item.BookingId }}</td>
<td>{{ item.BDate }}</td>
<td>{{ item.BTime }}</td>
<td>{{ item.Username }}</td>
<td>{{ item.Mobileno }}</td>
<td v-if="item.Drivermobile===''"><select @change="onChange($event)" class="form-control select2" style="width: 100%;" required>
<option  v-for="dept in listDriver" v-bind:value="dept.driver_phone">
{{dept.Remarks}}
</option>
</select></td>
<td v-else><select class="form-control" style="width: 100%;" disabled>
<option ref="item.BookingId" value="item.Drivermobile">{{item.DRemarks}}</option>
</select></td>
<td v-if="item.Drivermobile===''">
<div class="btn-group"><button class="btn btn-success btn-sm" v-on:click="deleteItem(item.BookingId,item.Mobileno,item.LocalAdd1,item.LocalAdd2,index)">Assign</button>
<button class="btn btn-primary btn-sm" v-on:click="openModal(item)">Info</button>
<button class="btn btn-danger btn-sm" v-on:click="deleteBooking(item.BookingId,item.Mobileno,'NS')">NS</button>
<button class="btn btn-warning text-danger btn-sm" v-on:click="deleteBooking(item.BookingId,item.Mobileno,'ND')">ND</button>
</div>                    
</td>
<td v-else>
<div class="btn-group"><button class="btn btn-primary btn-sm" v-on:click="openModal(item)">Info</button>
<button class="btn btn-danger btn-sm" v-on:click="deleteBooking(item.BookingId,item.Mobileno,'NS')">NS</button>
<button class="btn btn-warning text-danger btn-sm" v-on:click="deleteBooking(item.BookingId,item.Mobileno,'ND')">ND</button>
</div>                    
</td>

</tr>
</tbody>

</table>

</div>
<!-- /.card-body -->
</div>
</div>

</div>
</div>


</template>

<script>
import { mdbModal, mdbModalHeader, mdbModalTitle, mdbModalBody, mdbModalFooter, mdbBtn } from 'mdbvue'
export default {
  components: {
    mdbModal,
    mdbModalHeader,
    mdbModalTitle,
    mdbModalBody,
    mdbModalFooter,
    mdbBtn
  },
  name: 'AssignDrivervw',
  data () {
    return {
      modal: false,
      items: [],
      search: '',
      listDriver: [],
      data: {},
      drivermobno: '',
      timer: '',
      drivermobno1: ''
    }
  },
  computed:
  {
    filteredCustomers: function () {
      var self = this
      return this.items.filter(function (cust) { return cust.BookingId.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 })
      // return this.customers;
    }
  },
  created: function () {
    this.FetchItem()
    this.timer = setInterval(this.FetchItem, 20000)
  },
  methods: {
    audioClick () {
      var audio = new Audio('https://donkeycargo.com/alert.mp3') // path to file
      audio.play()
    },
    FetchItem () {
      let uri1 = this.$hostName + '/api/driver/viewDriver'
      this.axios.get(uri1, { params: { assign: 'driver' } }).then((response) => {
        this.listDriver = response.data
      })

      let uri = this.$hostName + "/api/driver/NoAssignBooking?drop=''"
      this.axios.get(uri).then((response) => {
        if (this.items.length !== response.data.length) {
          this.audioClick()
        }
        this.items = response.data
      })
    },
    deleteItem (id1, mobno, hno, area, ri) {
      var r = confirm('Do you want to Assign Booking?')
      if (r === true) {
        let uri = this.$hostName + '/api/courier/DriverAssign'
        this.data.booktype = 'Courier'
        this.data.bid = id1
        this.data.userid = ''
        this.data.mobno = mobno
        this.data.driverid = ''
        this.data.drivermobile = this.drivermobno
        this.data.hno = hno
        this.data.area = area
        this.axios.post(uri, this.data).then((response, err) => {
          if (response.data.status === 'success') {
            // window.location.reload()
          } else alert('something went wrong..contact Administrator..!')
        })
      }
    },
    deleteBooking (id1, mobno, type) {
      var r = ''
      if (type === 'ND') {
        r = confirm('Do you want to send ND SMS to user?')
      } else {
        r = confirm('Do you want to delete booking?')
      }
      if (r === true) {
        let uri = this.$hostName + '/api/courier/cancelBooking'
        this.data.usertype = 'admin'
        this.data.bid = id1
        this.data.userid = ''
        this.data.mobno = mobno
        this.data.driverid = ''
        this.data.drivermobile = this.drivermobno
        this.data.ctype = type
        this.axios.post(uri, this.data).then((response, err) => {
          if (response.data.status === 'success') {
            // window.location.reload()
          } else alert('something went wrong..contact Administrator..!')
        })
      }
    },
    onChange (event) {
      this.drivermobno = event.target.value
    },
    openModal (item) {
      this.$bus.$emit('modal-open', {
        title: 'Booking Information',
        description1: 'Booking ID : ' + item.BookingId,
        description2: 'From Address : ' + item.FromAddress,
        description3: 'To Address : ' + item.ToAddress,
        description4: 'Receiver Name : ' + item.RecName,
        description5: 'Receiver Address : ' + item.LocalAdd1 + ' ' + item.LocalAdd2 + ' ' + item.LocalAdd3,
        description6: 'Product Type : ' + item.ProductType,
        description7: 'No. of Items : ' + item.NOI,
        description8: 'Total KMS : ' + item.LocalDistance,
        description9: 'Item description & consignment value : ' + item.Remarks,
        description10: 'Payment Mode : ' + item.PaymentMode,
        description11: 'Net Total : ' + item.NetTotal,
        type: 'modal-primary',
        confirmText: '',
        cancelText: 'Cancel',
        confirmBefore: () => {
          // window.alert('저장 후 닫기 이후 훅')
        },
        cancelBefore: () => {
          // window.alert('닫기 이후 훅')
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



