
const state = {
  main: {
    name: 'Admin Panel',
    position: 'Web Developer',
    state: {
      color: '#3c763d',
      name: 'Online'
    },
    isAuth: false,
    createdAt: new Date().toLocaleString()
  }
}

const mutations = {
  setMut: (state, payload) => {
    console.log('hiii')
    state.main.isAuth = true
  }
}

export default {
  state,
  mutations
}



