<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Employees
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Employees</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Employees</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Employee Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Employee Name" v-model="employeename" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Designation</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Designation" v-model="designation" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Mobile No.</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Mobile No." v-model="mobno" maxlength="10" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Email ID</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Email ID" v-model="emailid" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Password</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Enter Password" v-model="password" required>
                  </div>
                   <div class="form-group">
                    <label for="exampleInputPassword1">Employee Type</label>
                    <select class="form-control select2" style="width: 100%;" v-model="emptype" v-autofocus required>
                    <option value="">Select EmployeeType</option>
                    <option value="Admin">Admin</option>
                    <option value="User">User</option>
                    </select>                    
                  </div>                  
                </div>
                <!-- /.card-body -->

                <div class="card-footer">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'Employeesadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', employeename: '', designation: '', mobno: '', emailid: '', emptype: '', password: '', listCategory: []
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      let uris = this.$hostName + '/common/Employees'
      this.axios.get(uris).then((response) => {
        this.listCategory = response.data
      })
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/common/Employees/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/common/Employees/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.employeename = response.data[0]['EmployeeName']
          this.designation = response.data[0]['Designation']
          this.mobno = response.data[0]['MobileNo']
          this.emailid = response.data[0]['EmailId']
          this.emptype = response.data[0]['EmployeeType']
          this.password = response.data[0]['Password']
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/common/Employees/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/common/Employees/update' }
      this.item.id = this.id
      this.item.employeename = this.employeename
      this.item.designation = this.designation
      this.item.mobno = this.mobno
      this.item.emailid = this.emailid
      this.item.emptype = this.emptype
      this.item.password = this.password
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'Employeesvw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



