<template>
  <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Coupon Master
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Coupon Master</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

      <!-- Content Body-->

      <div class="col-md-12">
        <div class="card">
          <div class="card-header">
            <div class="row">
                  <div class="col-sm-6"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
                  <div class="col-sm-6"><h3 class="card-title text-right"><router-link :to="{ name: 'Couponadd' }" class="btn btn-primary">Add Coupon Master</router-link></h3></div>
              </div>
          </div>
          <!-- /.card-header -->
          <div class="card-body p-0">
            <table class="table table-condensed">
              <tr>
                <th style="width: 5%">#</th>
                <!-- <th style="width: 20%">ServiceCity</th>
                <th style="width: 15%">ServiceType</th> -->
                <th style="width: 10%">CouponType</th>
                <th style="width: 10%">User Mobile</th>
                <th style="width: 15%">CouponCode</th>
                <th style="width: 10%">CouponRate</th>
                <th style="width: 15%">StartDate</th>
                <th style="width: 10%">Validity</th>
                <th style="width: 5%;">Action</th>
              </tr>
              <tbody>
                <tr v-for="(item,index) in filteredCustomers" :key="index">
                  <td>{{ item.id }}</td>
                  <!-- <td>{{ item.ServiceCity }}</td>
                  <td>{{ item.ServiceType }}</td> -->
                  <td>{{ item.CouponType }}</td>
                  <td>{{ item.user_mobile }}</td>
                  <td>{{ item.CouponCode }}</td>
                  <td>{{ item.CouponAmt }}</td>                  
                  <td>{{ item.start_date }}</td>                  
                  <td>{{ item.Validity }}</td>                  
                  <td>
                    <router-link :to="{name: 'Couponadd',params:{id: item.id}}" class="btn btn-primary">Edit</router-link>
                  </td>
                  <td>
                    <button class="btn btn-danger" v-on:click="deleteItem(item.id,index)">Delete</button>
                  </td>
                </tr>
              </tbody>

            </table>
          </div>
          <!-- /.card-body -->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import moment from 'moment'
export default {
  name: 'Couponvw',
  data () { return {items: [], search: ''} },
  components: {
    moment
  },
  computed:
  {
    filteredCustomers: function () {
      var self = this
      return this.items.filter(function (cust) { return cust.ServiceCity.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.ServiceType.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.CouponType.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.CouponCode.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || (cust.user_mobile && cust.user_mobile.toLowerCase().indexOf(self.search.toLowerCase()) >= 0) })
     // return this.customers;
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: { FetchItem () {
    let uri = this.$hostName + '/advanced/Coupon'
    this.axios.get(uri).then((response) => {
      this.items = response.data.map(m => {
        return {
          ...m,
          start_date: moment(m.start_date).format('DD/MM/YYYY')
        }
      })
    })
  },
    deleteItem (id1, ri) {
      var r = confirm('Do you want to Delete?')
      if (r === true) {
        let uri = this.$hostName + '/advanced/Coupon/Del/' + id1
        this.items.splice(ri, 1)
        this.axios.get(uri).then((response) => {
  // console.log(response.data);
          this.$router.push({name: 'Couponvw'})
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">

</style>



