<template>
<div class="Admin-view">
<row>
<div class="col-lg-3 col-xs-6">
<va-small-box
color="aqua"          
icon="ion-bag"
:title="this.items[0].nbooking"
description="No. of Bookings"
moreText="More info"
>
</va-small-box>
</div>
<div class="col-lg-3 col-xs-6">
<va-small-box
color="yellow"
icon="ion-person-add"
:title="this.items[0].ncancelbooking"
description="No. of Cancel Booking"
moreText="More info">
</va-small-box>
</div>
<div class="col-lg-3 col-xs-6">
<va-small-box
color="green"
icon="ion-stats-bars"
:title="this.items[0].ncompletebooking"
description="No. of Completed Booking"
moreText="More info">
</va-small-box>
</div>      
<div class="col-lg-3 col-xs-6">
<va-small-box
color="red"
icon="ion-pie-graph"
:title="this.items[0].nincompletebooking"
description="No. of Incompleted Booking"
moreText="More info">
</va-small-box>
</div>
<div class="col-lg-3 col-xs-6">
<va-small-box
color="green"
icon="ion-person-add"
:title="this.items[0].nusers"
description="No. of Users"
moreText="More info">
</va-small-box>
</div>      
<div class="col-lg-3 col-xs-6">
<va-small-box
color="aqua"          
icon="ion-bag"
:title="this.items[0].ndrivers"
description="No. of Drivers"
moreText="More info"
>
</va-small-box>
</div>
<div class="col-lg-3 col-xs-6">
<va-small-box
color="red"          
icon=""
title="Track Order"
description="See User & Driver's Location"
moreText="More info"
>
</va-small-box>
</div>

</row>

<row>
<section class="col-lg-7 connectedSortable ui-sortable">

</section>
<section class="col-lg-5 connectedSortable ui-sortable">
<!-- <va-calendar></va-calendar> --> 
</section>
</row>        

</div>
</template>

<script>
import VASmallBox from '../components/widgets/VASmallBox.vue'
import VAChatBox from '../components/widgets/VAChatBox.vue'
import VATodoList from '../components/widgets/VATodoList.vue'
import VAQuickMail from '../components/widgets/VAQuickMail.vue'
import VACalendar from '../components/widgets/VACalendar.vue'

export default {
  name: 'dashboard',
  data () {
    return {
      items: {}
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: {
    FetchItem () {
      let uri = this.$hostName + '/api/admin/overallBooking'
      this.axios.get(uri).then((response) => {
        this.items = response.data
      })
    }
  },
  components: {
    'va-small-box': VASmallBox,
    'va-chat-box': VAChatBox,
    'va-todo-list': VATodoList,
    'va-quick-mail': VAQuickMail,
    'va-calendar': VACalendar
  }
}

</script>



