<template>
  <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Complaints
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Complaints</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

      <!-- Content Body-->

      <div class="col-md-12">
        <div class="card">
          <div class="card-header">
            <div class="row">
                  <div class="col-sm-6"><h4 class="text-left">Search : <input type="text" name="" v-model="search" placeholder="Type Here..."></h4></div>
                  <div class="col-sm-6"><h3 class="card-title text-right"><router-link :to="{ name: 'Complaintsadd' }" class="btn btn-primary">Add Complaints</router-link></h3></div>
              </div>
          </div>
          <!-- /.card-header -->
          <div class="card-body p-0">
            <table class="table table-condensed">
              <tr>
                <th style="width: 5%">#</th>
                <th style="width: 20%">BookingID</th>
                <th style="width: 25%">Date&Time</th>
                <th style="width: 10%">Priority</th>
                <th style="width: 25%">Summary</th>
                <th style="width: 10%">Status</th>
                <th style="width: 10%;">Action</th>
              </tr>
              <tbody>
                <tr v-for="(item,index) in filteredCustomers">
                  <td>{{ item.id }}</td>
                  <td>{{ item.BookingID }}</td>
                  <td>{{ item.Datee }}</td>
                  <td>{{ item.Priority }}</td>
                  <td>{{ item.Summary }}</td>
                  <td>{{ item.Status===0?'Pending':'Resolved' }}</td>
                  <td v-if="item.Status===0">
                    <button class="btn btn-success" v-on:click="resolveItem(item.id,index)">Resolve</button>
                  </td>
                  <td>
                    <router-link :to="{name: 'Complaintsadd',params:{id: item.id}}" class="btn btn-primary">Edit</router-link>
                  </td>
                  <td>
                    <button v-if="item.Status===0" class="btn btn-danger" v-on:click="deleteItem(item.id,index)">Delete</button>
                  </td>                  
                </tr>
              </tbody>

            </table>
          </div>
          <!-- /.card-body -->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'Complaintsvw',
    data () { return {items: [], search: '', data: {}} },
    computed:
    {
      filteredCustomers: function () {
        var self = this
        return this.items.filter(function (cust) { return cust.BookingID.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.Priority.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.Status1.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 || cust.Summary.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 })
     // return this.customers;
      }
    },
    created: function () {
      this.FetchItem()
    },
    methods: { FetchItem () {
      let uri = this.$hostName + '/advanced/Complaints'
      this.axios.get(uri).then((response) => {
        this.items = response.data
      })
    },
      deleteItem (id1, ri) {
        var r = confirm('Do you want to Delete?')
        if (r === true) {
          let uri = this.$hostName + '/advanced/Complaints/Del/' + id1
          this.items.splice(ri, 1)
          this.axios.get(uri).then((response) => {
  // console.log(response.data);
            this.$router.push({name: 'Complaintsvw'})
          })
        }
      },
      resolveItem (id1, ri) {
        var r = confirm('Are u sure?')
        if (r === true) {
          let uri = this.$hostName + '/advanced/Complaints/statusChange/' + id1
          this.axios.get(uri).then((response) => {
            this.$router.go()
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">

</style>



