<template>
  <div class="Admin-view">
    <!-- Content Header (Page header) -->
    <section class="content-header">
      <div class="row">
        <div class="col-md-9">
          <h2>
            Booking History
          </h2>
        </div>
        <ol class="breadcrumb">
          <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
          <li class="active">Booking History</li>
        </ol>
      </div>
    </section>
    <!-- /.content-header -->

    <!-- Content Body-->

    <div class="col-md-12">
      <div class="card">
        <div class="card-header">
          <div class="row">
            <div class="col-sm-4">
              <h4 class="text-left">Search : <input
                  type="text"
                  name=""
                  v-model="search"
                  placeholder="Type Here..."
                ></h4>
            </div>
            <div class="col-sm-1">
              <h4 class="text-left">From : </h4>
            </div>
            <div class="col-sm-3">
              <h4 class="text-left">
                <datepicker
                  :value="fromdate"
                  placeholder="None"
                  format="dd-MM-yyyy"
                  @selected="onDateSelect1"
                  style="width:50px"
                ></datepicker>
              </h4>
            </div>

            <div class="col-sm-1">
              <h4 class="text-left">To : </h4>
            </div>
            <div class="col-sm-1">
              <h4 class="text-left">
                <div style="width:100px">
                  <datepicker
                    :value="todate"
                    placeholder="None"
                    format="dd-MM-yyyy"
                    @selected="onDateSelect2"
                    style="width:150px"
                  ></datepicker>
                </div>
              </h4>
            </div>
            <div
              class="col-sm-12"
              style="float:right;margin-right:10px;"
            >
              <h4 class="text-left"><button
                  class="btn-success"
                  type="button"
                  v-on:click="FetchItem()"
                >Submit</button></h4>
            </div>
            <date-picker v-model="card"></date-picker>
          </div>
        </div>
        <!-- /.card-header -->
        <div class="card-body p-0">
          <table class="table table-condensed fixed-layout">
            <tr>
              <th style="width: 8%">BookingID</th>
              <th style="width: 5%">Book Type</th>
              <th style="width: 8%">Book Date</th>
              <th style="width: 8%">Book Time</th>
              <th style="width: 15%">User Name</th>
              <th style="width: 10%">User Email</th>
              <th style="width: 8%">User Mobile</th>
              <th style="width: 8%">Drop Mobile</th>
              <th style="width: 15%">Driver Name</th>
              <th style="width: 10%">Driver Mobile</th>
              <th style="width: 12%">Pay Mode</th>
              <th style="width: 10%">Amount</th>
            </tr>
            <tbody>
              <tr v-for="(item,index) in filteredCustomers" :key="index">
                <td><a v-on:click="openModal(item)">{{ item.BookingId }}</a></td>
                <td>{{ item.bookingtype }}</td>
                <td>{{ item.BDate }}</td>
                <td>{{ item.BTime }}</td>
                <td>{{ item.Name }}</td>
                <td>{{ item.Email }}</td>
                <td>{{ item.UserMobileno }}</td>
                <td>{{ item.RecMobile }}</td>
                <td>{{ item.driver_name }}</td>
                <td>{{ item.Drivermobile }}</td>
                <td>{{ item.PaymentMode }}</td>
                <td>{{ item.driverEarnings }}</td>

              </tr>
            </tbody>

          </table>
        </div>
        <!-- /.card-body -->
      </div>
    </div>
  </div>
  </div>

</template>

<script>
import Datepicker from 'vuejs-datepicker'
import datePicker1 from 'vue-bootstrap-datetimepicker'
import moment from 'moment'
import { mdbModal, mdbModalHeader, mdbModalTitle, mdbModalBody, mdbModalFooter, mdbBtn } from 'mdbvue'

export default {
  components: {
    mdbModal,
    mdbModalHeader,
    mdbModalTitle,
    mdbModalBody,
    mdbModalFooter,
    mdbBtn,
    datePicker1,
    Datepicker
  },
  name: 'AssignDrivervw',
  data () { return { items: [], search: '', listDriver: [], data: {}, drivermobno: '', fromdate: '', todate: '', myDate: '' } },
  computed:
  {
    filteredCustomers: function () {
      var self = this
      return this.items.filter(function (cust) { return cust.BookingId.toLowerCase().indexOf(self.search.toLowerCase()) >= 0 })
      // return this.customers;
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: {
    FetchItem () {
      // console.log();
      console.log(this.fromdate)
      console.log(this.todate)
      let uri = this.$hostName + '/api/driver/DriverReport'
      this.data.fromdate = this.fromdate
      this.data.todate = this.todate
      this.axios.post(uri, this.data).then((response) => {
        console.log(response.data)
        this.items = response.data
      })
    },
    deleteItem (id1, mobno, ri) {
      var r = confirm('Do you want to Assign Booking?')
      if (r === true) {
        let uri = this.$hostName + '/api/courier/DriverAssign'
        this.data.booktype = 'Courier'
        this.data.bid = id1
        this.data.userid = ''
        this.data.mobno = mobno
        this.data.driverid = ''
        this.data.drivermobile = this.drivermobno
        console.log(this.data)
        this.axios.post(uri, this.data).then((response) => {
          // console.log(response.data);
          location.reload()
        })
      }
    },
    fixDate (event) {
      alert(moment(event).format('YYYY-MM-DD'))
    },
    onDateSelect1 (date) {
      this.fromdate = moment(date).format('YYYY/MM/DD')
    },
    onDateSelect2 (date) {
      this.todate = moment(date).format('YYYY/MM/DD')
    },
    openModal (item) {
      console.log(this.$bus)
      let uri = this.$hostName + '/api/driver/NoAssignBooking?BookingId=?'
      this.axios.get(uri, { params: { bookingid: item.BookingId } }).then((response) => {
        let info = response.data[0]
        console.log(info)
        this.$bus.$emit('modal-open', {
          title: 'Booking Information',
          description1: 'Booking ID : ' + info.BookingId,
          description2: 'From Address : ' + info.FromAddress,
          description3: 'To Address : ' + info.ToAddress,
          description4: 'Receiver Name : ' + info.RecName,
          description5: 'Receiver Address : ' + info.LocalAdd1 + ' ' + info.LocalAdd2 + ' ' + info.LocalAdd3,
          description6: 'Product Type : ' + info.ProductType,
          description7: 'No. of Items : ' + info.NOI,
          description8: 'Total KMS : ' + info.LocalDistance,
          description9: 'Item description & consignment value : ' + info.Remarks,
          description10: 'Payment Mode : ' + info.PaymentMode,
          description11: 'Net Total : ' + info.NetTotal,
          type: 'modal-primary',
          confirmText: '',
          cancelText: 'Cancel',
          confirmBefore: () => {
            // window.alert('저장 후 닫기 이후 훅')
          },
          cancelBefore: () => {
            // window.alert('닫기 이후 훅')
          }
        })
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



