<template>
  <div class="Admin-view">

    <section class="content-header">
      <div class="row">
        <div class="col-md-9">
          <h2>
            Coupon Master
          </h2>
        </div>
        <ol class="breadcrumb">
          <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
          <li class="active">Coupon Master</li>
        </ol>
      </div>
    </section>
    <!-- /.content-header -->

    <!-- Content Body-->
    <div class="row">
      <div class="col-md-3"></div>
      <div class="col-md-6">
        <div class="card card-primary">
          <div class="card-header">
            <h3 class="card-title">Add Coupon Master</h3>
          </div>
          <!-- /.card-header -->
          <!-- form start -->
          <form
            role="form"
            v-on:submit.prevent="addItem"
            method="post"
            enctype="multipart/form-data"
          >
            <div class="card-body">
              <div class="form-group">
                <label for="exampleInputEmail1">ID</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputEmail1"
                  disabled
                  v-model="id"
                >
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">Coupon Type</label>
                <select
                  class="form-control select2"
                  style="width: 100%;"
                  v-model="couponType"
                  @change="onChange"
                  v-autofocus
                  required
                >
                  <option value="">Select Coupon Type</option>
                  <option value="Starter">Starter</option>
                  <option value="Individual">Individual</option>
                  <!-- <option value="Business">Business</option>
                    <option value="Referral">Referral</option> -->
                </select>
              </div>
                <div
                class="form-group"
                v-if="couponType==='Individual'"
              >
                 <label for="exampleInputPassword1">User MobileNo.</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputPassword1"
                  placeholder="Enter User's mobile number"
                  v-model="userMobile"    
                  maxlength="10"              
                >
              </div>
              <div
                class="form-group"
                v-if="offer1"
              >
                <label for="exampleInputPassword1">Service City</label>
                <select
                  class="form-control select2"
                  style="width: 100%;"
                  v-model="selectedcity"
                  required
                >
                  <option value="">Select Service City</option>
                  <option
                    v-for="(dept,index) in listCity"
                    v-bind:value="dept.ServiceArea"
                    :key="index"
                  >
                    {{dept.ServiceArea}}
                  </option>
                </select>
              </div>
              <div
                class="form-group"
                v-if="offer1"
              >
                <label for="exampleInputPassword1">Service Type</label>
                <select
                  class="form-control select2"
                  style="width: 100%;"
                  v-model="selectedType"
                  required
                >
                  <option value="">Select Service Type</option>
                  <option value="Courier">Courier</option>
                  <option value="Parcel">Parcel</option>
                  <option value="Rental">Rental</option>
                </select>
              </div>
              <div
                class="form-group"
                v-if="offer1"
              >
                <label for="exampleInputPassword1">Monthly Limit (Rs.)</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputPassword1"
                  placeholder="Enter Monthly Limit (in Rs.)"
                  v-model="monthly"
                  required
                >
              </div>
              <!-- <div class="form-group">
                    <label for="exampleInputPassword1">Type</label>
                    <select class="form-control select2" style="width: 100%;" @change="onChange" v-model="selectedType" required>
                    <option selected="selected" value="Offers">Offers</option>
                    <option value="Updates">Updates</option>
                    </select>                    
                  </div> -->
              <div class="form-group">
                <div class="row">
                  <div class="col-md-6"><label for="exampleInputPassword1">Coupon Code</label>
                    <input
                      type="text"
                      class="form-control"
                      id="exampleInputPassword1"
                      placeholder="Enter Coupon Code"
                      v-model="offercode"
                      required
                    >
                  </div>
                  <div class="col-md-6"><label for="exampleInputPassword1">Coupon Percentage</label>
                    <input
                      type="text"
                      class="form-control"
                      id="exampleInputPassword1"
                      placeholder="Enter Coupon %"
                      v-model="offerrate"
                      required
                    >
                  </div>
                </div>
              </div>
              <div class="form-group">
                <div class="row">
                  <div class="col-md-12"><label for="examplestartdate1">Start Date</label>
                    <input
                      type="date"
                      class="form-control"
                      id="examplestartdate1"
                      placeholder="Enter Coupon Code"
                      v-model="startDate"
                      required
                    >
                  </div>
                </div>
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">Coupon Validity</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputPassword1"
                  placeholder="Enter Validity (in Days)"
                  v-model="offervalidity"
                  required
                >
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">Content</label>
                <textarea
                  class="form-control"
                  id="exampleInputPassword1"
                  placeholder="Type Here..."
                  v-model="note"
                  required
                ></textarea>
              </div>
              <div class="form-group row">
                <div class="col-md-6">
                  <label for="exampleInputPassword1">Image Upload</label>
                  <input
                    type="file"
                    accept="image/*"
                    class="form-control"
                    id="exampleInputPassword1"
                    @change="imageChanged"
                    name="files"
                  >
                  <div
                    class="col-md-12"
                    style="padding-top: 1em"
                  >
                    <progress
                      :value.prop="uploadPercentage"
                      max="100"
                      v-if="isProgress"
                    ></progress>
                  </div>
                </div>
                <div class="col-md-6">
                  <img
                    :src="product.image"
                    width="230"
                    height="180"
                  >
                  <!-- <img :src="product.image" width="230" height="180"> -->
                </div>
              </div>
            </div>
            <!-- /.card-body -->

            <div class="card-footer">
              <button
                type="submit"
                class="btn btn-primary"
              >Submit</button>
            </div>
          </form>
        </div>
        <!-- /.card -->
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  name: 'Couponadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {
      item: {}, id: '', listCity: [], selectedcity: '', offer1: false, selectedType: '', couponType: 'Starter', product: { image: '' }, offercode: '', offerrate: '', offervalidity: '', note: '', uploadPercentage: 0, isProgress: false, frmdata: '', monthly: '', startDate: null, userMobile: ''
    }
  },
  created: function () { this.autoInc() },
  methods: {
    imageChanged (e) {
      this.product.files = e.target.files[0]
      var fileReader = new FileReader()

      fileReader.readAsDataURL(e.target.files[0])
      fileReader.onload = (e) => {
        this.product.image = e.target.result
      }
    },
    onChange: function () {
      if (this.couponType === 'Business') { this.offer1 = true } else { this.offer1 = false }
      return
    },
    autoInc () {
      let uris = this.$hostName + '/common/ServiceArea'
      this.axios.get(uris).then((response) => {
        this.listCity = response.data
      })
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/advanced/Coupon/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/advanced/Coupon/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
          // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.selectedcity = response.data[0]['ServiceCity']
          this.selectedType = response.data[0]['ServiceType']
          this.couponType = response.data[0]['CouponType']
          this.monthly = response.data[0]['MonthlyLimit']
          this.offer1 = false
          if (response.data[0]['CouponType'] === 'Business') { this.offer1 = true }
          this.offercode = response.data[0]['CouponCode']
          this.offerrate = response.data[0]['CouponAmt']
          this.offervalidity = response.data[0]['Validity']
          this.note = response.data[0]['Description']
          this.product.image = response.data[0]['filePath']
          this.startDate = moment(response.data[0]['start_date']).format('YYYY-MM-DD')
          this.userMobile = response.data[0]['user_mobile']

          imageChanged()
          onChange()
          // console.log(this.product.image)
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let data = new FormData()
      let file = this.product.files
      data.append('file', file)
      this.isProgress = true
      let uri = this.$hostName + '/advanced/Coupon/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/advanced/Coupon/update' }
      data.append('id', this.id)
      data.append('selectedcity', this.selectedcity)
      data.append('selectedType', this.selectedType)
      data.append('couponType', this.couponType)
      data.append('offercode', this.offercode)
      data.append('offerrate', this.offerrate)
      data.append('offervalidity', this.offervalidity)
      data.append('note', this.note)
      data.append('monthly', this.monthly)
      data.append('start_date', this.startDate)
      data.append('user_mobile', this.userMobile)
      this.axios.post(uri, data, {
        onUploadProgress: function (progressEvent) {
          this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
        }.bind(this)
      }).then((response) => { this.$router.push({ name: 'Couponvw' }) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



