import Vue from 'vue'
import VueCookie from 'vue-cookie'
import Router from 'vue-router'
import AdminRoutes from './admin'
import UserRoutes from './user'
import CourierRoutes from './courier'
import ParcelRoutes from './parcel'
import SharingRoutes from './sharing'
import RentalRoutes from './rental'
import LandingRoutes from './landing'
import AdvancedRoutes from './advanced'
import AgentRoutes from './agent'
// import store from '../vuex/store'
// import CommonRoutes from './common'

Vue.use(Router)
Vue.use(VueCookie)

Vue.prototype.$isAuth = 0
Vue.prototype.$hostName = 'https://donkey-cargo.xyz'
// Vue.prototype.$hostName = 'http://localhost:4000'
// Vue.prototype.$hostName = 'http://172.105.39.173:4000'
const baseRoute = []

const routes = baseRoute.concat(AdminRoutes, UserRoutes, CourierRoutes, LandingRoutes, ParcelRoutes, SharingRoutes, RentalRoutes, AdvancedRoutes, AgentRoutes)

const router = new Router({
  mode: 'history',
  routes,
  base: '/Admin'
})

router.beforeEach((to, from, next) => {
  console.log(VueCookie.get('isAuth'))
  next()

  // if (to.meta.requiresAuth) {
  //   const authUser = JSON.parse(window.localStorage.getItem('authUser'))
  //   if (authUser && authUser.access_token) {
  //     next()
  //   } else {
  //     next({name: 'Dashboard'})
  //   }
  // }console.log(store.commit('setMut'))
})

export default router



