import UserDashboard from '../components/user/Dashboard.vue'
import Login from '../Login.vue'

const UserRoutes = [
  {
    path: '/user',
    name: 'Dashboard',
    component: UserDashboard
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  }
]

export default UserRoutes



