<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h3> Booking Statement     </h3>
            </div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Booking Statement</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->

<div class="col-md-12">
<div class="card">
              <!-- /.card-header -->
              <div class="card-body p-0">
                <v-client-table 
:data="tableData"
:columns="columns" 
:options="options">
</v-client-table>
              </div>
              <!-- /.card-body -->
            </div>
</div>
</div>
</div>

</template>

<script>

export default {
  name: 'DriverListvw',
  props: ['name'],
  data () {
    return {items: [],
      search: '',
      listDriver: [],
      data: {},
      drivermobno: '',
      columns: ['BookingId', 'BookingType', 'BDate', 'BTime', 'Username', 'Mobileno', 'RecMobile', 'driver_name', 'Drivermobile', 'PaymentMode', 'NetTotal'],
      tableData: [],
      options: {
        headings: {
          BookingId: 'Booking Id',
          BookingType: 'Booking Type',
          BDate: 'BDate',
          BTime: 'BTime',
          Username: 'UserName',
          Mobileno: 'User Mobile',
          RecMobile: "Drop User's Mobile",
          driver_name: 'Driver Name',
          Drivermobile: 'Driver Mobile',
          PaymentMode: 'PayMode',
          NetTotal: 'Amount'
        },
        sortable: ['BookingId', 'BookingType', 'BDate', 'BTime', 'Username', 'driver_name', 'PaymentMode', 'NetTotal'],
        filterable: ['BookingId', 'BookingType', 'BDate', 'Username', 'driver_name', 'PaymentMode', 'NetTotal']
      }
    }
  },
  created: function () {
    this.FetchItem()
  },
  methods: { FetchItem () {
    let uri1 = this.$hostName + '/api/driver/NoAssignBooking'
    if (this.$props.name === 'cancel') {
      uri1 = this.$hostName + "/api/driver/NoAssignBooking?cancel=''"
    }
    if (this.$props.name === 'complete') {
      uri1 = this.$hostName + "/api/driver/NoAssignBooking?complete=''"
    }
    if (this.$props.name === 'incomplete') {
      uri1 = this.$hostName + "/api/driver/NoAssignBooking?incomplete=''"
    }
    this.axios.get(uri1).then((response) => {
      this.tableData = response.data
    })
  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.col-md-9
{
  margin-top: -10px
}
</style>



