<template>
<div class="container row">
<div class="col-11">
<v-client-table 
:data="tableData"
:columns="columns" 
:options="options">
</v-client-table>
</div>
</div>

</template>

<script>

export default {
  data () {
    return {
      columns: ['driver_id', 'driver_name', 'driver_phone'],
      tableData: [],
      options: {
        headings: {
          driver_id: 'ID',
          driver_name: 'Name',
          driver_phone: 'Mobile'
        },
        sortable: ['driver_name', 'driver_phone'],
        filterable: ['driver_name', 'driver_phone']
      }
    }
  },
  mounted () {
    let uri1 = this.$hostName + '/api/driver/viewDriver'
    this.axios.get(uri1).then(res => {
      this.tableData = res.data
    })
  }

}
</script>




