<template>

  <div class="small-box" :class="bgColor" style="word-wrap: break-word;">
    <div class="inner">
      <p style="font-size: 40px">{{title}}</p>

      <p>{{description}}</p>
    </div>
    <div class="icon">
      <i class="ion" :class="icon"></i>
    </div>
    <a href="#" class="small-box-footer" @click="moreEvent">{{moreText}} <i class="fa fa-arrow-circle-right"></i></a>
  </div>
</template>

<script>
export default {
  name: 'SmallBox',
  props: ['color', 'icon', 'title', 'description', 'moreText'],
  computed: {
    bgColor () {
      return `bg-${this.color}`
    }
  },
  methods: {
    moreEvent () {
      if (this.description === 'Pending Bookings') {
        window.location.href = '/advanced/AssignDriver'
      }
      if (this.title === 'Track Order') {
        this.$router.push('/advanced/DriverMap')
      }
      if (this.description === 'No. of Bookings') {
        this.$router.push('/advanced/BookingStatement/all')
      }
      if (this.description === 'No. of Cancel Booking') {
        this.$router.push('/advanced/BookingStatement/cancel')
      }
      if (this.description === 'No. of Completed Booking') {
        this.$router.push('/advanced/BookingStatement/complete')
      }
      if (this.description === 'No. of Incompleted Booking') {
        this.$router.push('/advanced/BookingStatement/incomplete')
      }
      if (this.description === 'No. of Drivers') {
        this.$router.push('/advanced/DriverList')
      }
      this.$emit('more-click')
    }
  }
}
</script>



