<template lang="html">
  <!-- Modal -->
  <div class="modal fade" id="bsModal" tabindex="-1" role="dialog" aria-labelledby="bsModalLabel" data-toggle="modal" data-backdrop="static" data-keyboard="false">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
          <h4 class="modal-title" id="bsModalLabel">{{title}}</h4>
        </div>
        <div class="modal-body">
          <div style="padding:10px">{{description1}}</div>
          <div style="padding:10px">{{description2}}</div>
          <div style="padding:10px">{{description3}}</div>
          <div style="padding:10px">{{description4}}</div>
          <div style="padding:10px">{{description5}}</div>
          <div style="padding:10px">{{description6}}</div>
          <div style="padding:10px">{{description7}}</div>
          <div style="padding:10px">{{description8}}</div>
          <div style="padding:10px">{{description9}}</div>
          <div style="padding:10px">{{description10}}</div>
          <div style="padding:10px">{{description11}}</div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-default" @click="cancel">{{cancelText}}</button>
          <button type="button" class="btn btn-primary" v-if="confirmText === ''" @click="confirm">{{confirmText}}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// const ModalTypes = ['modal-primary', 'modal-success', 'modal-info', 'modal-warning', 'modal-danger']
export default {
  mounted () {
    this.$bus.$on('modal-open', (args) => {
      // TODO: args가 올바른지 확인해야함
      this.type = args.type
      this.title = args.title
      this.description1 = args.description1
      this.description2 = args.description2
      this.description3 = args.description3
      this.description4 = args.description4
      this.description5 = args.description5
      this.description6 = args.description6
      this.description7 = args.description7
      this.description8 = args.description8
      this.description9 = args.description9
      this.description10 = args.description10
      this.description11 = args.description11
      this.confirmText = args.confirmText || '저장 후 닫기'
      this.cancelText = args.cancelText || '닫기'
      this.confirmBeforeHook = args.confirmBefore
      this.confirmAfterHook = args.confirmAfter
      this.cancelBeforeHook = args.cancelBefore
      this.cancelAfterHook = args.cancelAfter

      $('#bsModal').modal('show')
    })
    $('#bsModal').on('show.bs.modal', (e) => {
    })

    $('#bsModal').on('hidden.bs.modal', (e) => {
      this.confirmAfterHook()
      this.cancelAfterHook()
      this.resetProperties()
    })
  },
  data () {
    return {
      type: '',
      title: '',
      description1: '',
      description2: '',
      description3: '',
      description4: '',
      description5: '',
      description6: '',
      description7: '',
      description8: '',
      description9: '',
      description10: '',
      description11: '',
      confirmText: '',
      cancelText: '',
      confirmBeforeHook: () => {},
      confirmAfterHook: () => {},
      cancelBeforeHook: () => {},
      cancelAfterHook: () => {}
    }
  },
  methods: {
    confirm: function () {
      this.confirmBeforeHook()
      $('#bsModal').modal('hide')
      this.cancelAfterHook = () => {}
    },
    cancel: function () {
      this.cancelBeforeHook()
      $('#bsModal').modal('hide')
      this.confirmAfterHook = () => {}
    },
    resetProperties: function () {
      this.type = ''
      this.title = ''
      this.description = ''
      this.confirmText = ''
      this.cancelText = ''
      this.confirmBeforeHook = () => {}
      this.confirmAfterHook = () => {}
      this.cancelBeforeHook = () => {}
      this.cancelAfterHook = () => {}
    }
  }
}
</script>

<style lang="css">
</style>



