<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2 style="margin-top: 5px">
        Driver Details
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">{{id? 'Update':'Add New '}} Driver</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">{{id?'Update ':'Add '}} Driver Details</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group" v-show="id">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>                  
                  <div class="row" style="margin-bottom:1rem">
                  <div class="col-md-7">
                    <label for="exampleInputPassword1">Driver ID</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type Driver ID" v-model="driverid" disabled required>
                  </div>
                  <div class="col-md-5">
                    <label for="exampleInputPassword1">Driver Type</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type Driver Type." v-model="drivertype" required>
                  </div>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Driver Name</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="drivername" placeholder="Type Driver name" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Driver Address</label>
                    <textarea class="form-control" id="exampleInputEmail1" v-model="driveraddress" placeholder="Type Driver Address" required></textarea>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Phone No.</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="drivermobno" placeholder="Type Driver Mobile" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Email ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="driveremail" placeholder="Type Driver Email" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Year of Experience</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="experience" placeholder="Type Driver's Experience" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Vehicle No.</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="vehicleno" placeholder="Type Vehicle No" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Vehicle Type</label>
                  <select class="form-control select2" style="width: 100%;" v-model="vehicletype" required>
                    <option value="">Select Vehicle Type</option>
                    <option value="Rent Vehicle">Rent Vehicle</option>
                    <option value="Own Vehicle">Own Vehicle</option>
                    </select>                    
                    </div>
                    <div class="form-group">
                    <label for="exampleInputPassword1">Driver Status</label>
                  <select class="form-control select2" style="width: 100%;" v-model="driverstatus" required>
                    <option value="">Select Driver Status</option>
                    <option value="Pending">Pending</option>
                    <option value="Approved">Approved</option>
                    <option value="Blocked">Blocked</option>
                    </select>                    
                    </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Remarks</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type in short" v-model="remarks">
                  </div>                  
                </div>
                <!-- /.card-body -->

                <div class="card-footer" v-if="status===0">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'EditDriveradd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', driverid: '', drivertype: 'Mad Money', drivername: '', driveraddress: '', drivermobno: '', driveremail: '', experience: '', vehicleno: '', vehicletype: '', driverstatus: '', remarks: '', status: 0
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id !== undefined) {
        let uri = this.$hostName + '/api/driver/viewDriver1'
        this.axios.get(uri, { params: { id: this.$route.params.id } }).then((response) => {
          this.item = response.data[0]
          this.id = this.item.id
          this.driverid = this.item.driver_id
          this.drivertype = this.item.drivertype
          this.drivername = this.item.driver_name
          this.driveraddress = this.item.driver_address
          this.drivermobno = this.item.driver_phone
          this.driveremail = this.item.driver_email
          this.experience = this.item.year_experience
          this.vehicleno = this.item.vehicle_number
          this.vehicletype = this.item.vehicle_type
          this.driverstatus = this.item.driverstatus
          this.remarks = this.item.Remarks
        })
      }
    },
    addItem () {
      let uri = this.id ? this.$hostName + '/api/Driver/updateDriver' : this.$hostName + '/api/Driver/AddDriver'
      this.item.id = this.id
      this.item.drivername = this.drivername
      this.item.drivertype = this.drivertype
      this.item.driveraddress = this.driveraddress
      this.item.drivermobileno = this.drivermobno
      this.item.driveremail = this.driveremail
      this.item.experience = this.experience
      this.item.vehiclenumber = this.vehicleno
      this.item.vehicletype = this.vehicletype
      this.item.driverstatus = this.driverstatus
      this.item.remarks = this.remarks

      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'DriverListvw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



