import Complaintsadd from '../components/advanced/Complaintsadd'
import Complaintsvw from '../components/advanced/Complaintsvw'
import MessageTemplateadd from '../components/advanced/MessageTemplateadd'
import MessageTemplatevw from '../components/advanced/MessageTemplatevw'
import CommonOffersadd from '../components/advanced/CommonOffersadd'
import CommonOffersvw from '../components/advanced/CommonOffersvw'
import SharePassadd from '../components/advanced/SharePassadd'
import SharePassvw from '../components/advanced/SharePassvw'
import FAQadd from '../components/advanced/FAQadd'
import FAQvw from '../components/advanced/FAQvw'
import Reasonadd from '../components/advanced/Reasonadd'
import Reasonvw from '../components/advanced/Reasonvw'
import Couponadd from '../components/advanced/Couponadd'
import Couponvw from '../components/advanced/Couponvw'
import AssignDrivervw from '../components/advanced/AssignDrivervw'
import DriverListvw from '../components/advanced/DriverListvw'
import Testvw from '../components/advanced/Testvw'
import BookingHistoryvw from '../components/advanced/BookingHistoryvw'
import DriverMap from '../components/advanced/DriverMap'
import BookingStatementvw from '../components/advanced/BookingStatementvw'
import EditDriveradd from '../components/advanced/EditDriveradd'

const AdvancedRoutes = [
  {
    path: '/advanced/MessageTemplate/add',
    name: 'MessageTemplateadd',
    component: MessageTemplateadd
  },
  {
    path: '/advanced/MessageTemplate',
    name: 'MessageTemplatevw',
    component: MessageTemplatevw
  },
  {
    path: '/advanced/CommonOffers/add',
    name: 'CommonOffersadd',
    component: CommonOffersadd
  },
  {
    path: '/advanced/CommonOffers',
    name: 'CommonOffersvw',
    component: CommonOffersvw
  },
  {
    path: '/advanced/SharePass/add',
    name: 'SharePassadd',
    component: SharePassadd
  },
  {
    path: '/advanced/SharePass',
    name: 'SharePassvw',
    component: SharePassvw
  },
  {
    path: '/advanced/FAQ/add',
    name: 'FAQadd',
    component: FAQadd
  },
  {
    path: '/advanced/FAQ',
    name: 'FAQvw',
    component: FAQvw
  },
  {
    path: '/advanced/Reason/add',
    name: 'Reasonadd',
    component: Reasonadd
  },
  {
    path: '/advanced/Reason',
    name: 'Reasonvw',
    component: Reasonvw
  },
  {
    path: '/advanced/Coupon/add',
    name: 'Couponadd',
    component: Couponadd
  },
  {
    path: '/advanced/Coupon',
    name: 'Couponvw',
    component: Couponvw
  },
  {
    path: '/advanced/AssignDriver',
    name: 'AssignDrivervw',
    component: AssignDrivervw
  },
  {
    path: '/advanced/DriverList',
    name: 'DriverListvw',
    component: DriverListvw
  },
  {
    path: '/advanced/BookingHistory',
    name: 'BookingHistoryvw',
    component: BookingHistoryvw
  },
  {
    path: '/advanced/Test',
    name: 'Testvw',
    component: Testvw
  },
  {
    path: '/advanced/DriverMap',
    name: 'DriverMap',
    component: DriverMap
  },
  {
    path: '/advanced/BookingStatement/:name',
    name: 'BookingStatementvw',
    component: BookingStatementvw,
    props: true
  },
  {
    path: '/advanced/Complaints',
    name: 'Complaintsvw',
    component: Complaintsvw,
    props: true
  },
  {
    path: '/advanced/Complaints/add',
    name: 'Complaintsadd',
    component: Complaintsadd
  },
  {
    path: '/advanced/DriverList/edit/:id',
    name: 'EditDriveradd',
    component: EditDriveradd
  },
  {
    path: '/advanced/DriverList/add',
    name: 'EditDriversave',
    component: EditDriveradd
  }
]

export default AdvancedRoutes



