<template>
<div class="Admin-view">

   <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h2>
        Complaints
      </h2></div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Complaints</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->
<div class="row">
  <div class="col-md-3"></div>
<div class="col-md-6">
 <div class="card card-primary">
              <div class="card-header">
                <h3 class="card-title">Add Complaints</h3>
              </div>
              <!-- /.card-header -->
              <!-- form start -->
              <form role="form" v-on:submit.prevent="addItem" method="post">
                <div class="card-body">
                  <div class="form-group">
                    <label for="exampleInputEmail1">ID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" disabled v-model="id">
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">BookingID</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" v-model="bookingid" placeholder="Type BookingID" v-autofocus required>
                  </div>
                  <div class="row" style="margin-bottom:1rem">
                  <div class="col-md-7">
                    <label for="exampleInputPassword1">Caller Name</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type Caller Name" v-model="callername" required>
                  </div>
                  <div class="col-md-5">
                    <label for="exampleInputPassword1">Caller Mobile</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type Caller MobileNo." v-model="callermobile" required maxlength="10">
                  </div>
                  </div>

                  <div class="form-group">
                    <label for="exampleInputPassword1">Priority</label>
                  <select class="form-control select2" style="width: 100%;" v-model="priority" required>
                    <option value="">Select Priority</option>
                    <option value="High">High</option>
                    <option value="Medium">Medium</option>
                    <option value="Low">Low</option>
                    </select>                    
                    </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Summary</label>
                    <input type="text" class="form-control" id="exampleInputPassword1" placeholder="Type in short" v-model="summary" required>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputPassword1">Description</label>
                    <textarea class="form-control" id="exampleInputPassword1" placeholder="Type Description" v-model="description" required></textarea>
                  </div>
                </div>
                <!-- /.card-body -->

                <div class="card-footer" v-if="status===0">
                  <button type="submit" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
            <!-- /.card -->
 </div></div>
 </div>
</div>
</template>

<script>
export default {
  name: 'Complaintsadd',
  directives: {
    'autofocus': {
      inserted (el) {
        el.focus()
      }
    }
  },
  data () {
    return {item: {}, id: '', bookingid: '', callername: '', callermobile: '', priority: '', summary: '', description: '', status: 0
    }
  },
  created: function () { this.autoInc() },
  methods: {
    autoInc () {
      if (this.$route.params.id === undefined) {
        let uri = this.$hostName + '/advanced/Complaints/Auto'
        this.axios.get(uri).then((response) => {
          this.id = response.data[0]['id']
          this.item.id = this.id
        })
      } else {
        let uri = this.$hostName + '/advanced/Complaints/edit/' + this.$route.params.id
        this.axios.get(uri).then((response) => {
                    // this.item = response.data;
          this.id = response.data[0]['id']
          this.item.id = this.id
          this.bookingid = response.data[0]['BookingID']
          this.callername = response.data[0]['CallerName']
          this.callermobile = response.data[0]['CallerMobile']
          this.priority = response.data[0]['Priority']
          this.summary = response.data[0]['Summary']
          this.description = response.data[0]['Description']
          this.status = response.data[0]['Status']
        })
      }
      this.$refs.search.focus()
    },
    addItem () {
      let uri = this.$hostName + '/advanced/Complaints/add'
      if (this.$route.params.id !== undefined) { uri = this.$hostName + '/advanced/Complaints/update' }
      this.item.id = this.id
      this.item.bookingid = this.bookingid
      this.item.callername = this.callername
      this.item.callermobile = this.callermobile
      this.item.priority = this.priority
      this.item.summary = this.summary
      this.item.description = this.description
      this.axios.post(uri, this.item).then((response) => { this.$router.push({name: 'Complaintsvw'}) })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



