<template>
    <div class="Admin-view">
  <!-- Content Header (Page header) -->
      <section class="content-header">
      <div class="row">
        <div class="col-md-9">
      <h3>        Driver's Location      </h3>
            </div>
      <ol class="breadcrumb">
        <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
        <li class="active">Driver's Location</li>
      </ol>
    </div>
    </section>
    <!-- /.content-header -->

<!-- Content Body-->

<div class="col-md-12">
<div class="card">
              <!-- /.card-header -->
              <div class="card-body p-0">
                
                <div>
    <div class="row">
<div class="col-md-2">
      <h4>Search Location</h4>
      </div>
      <div class="col-md-10" style="margin-top:5px;">
        <gmap-autocomplete
          @place_changed="setPlace">
        </gmap-autocomplete>
        <input v-model="bookingid" placeholder="Enter BookingId" />
        <button @click="addMarker">Search</button>
        <button @click="FetchItem">Refresh</button>
      </div>
      <br/>

    </div>

    <br>
    <gmap-map ref="mymap" :center="center" :zoom="10" style="width: 100%; height: 450px">

      <gmap-info-window :options="infoOptions" :position="infoPosition" :opened="infoOpened" @closeclick="infoOpened=false" style="white-space:pre-line;">
      <div style="white-space:pre-line">  {{infoContent}} </div>
      </gmap-info-window>

      <gmap-marker v-for="(item, key) in places" :key="index" :position="getPosition(item)" :clickable="true" @click="toggleInfo(item, key)" icon="https://img.icons8.com/color/32/000000/car.png" />
      <gmap-marker v-for="(item, key) in userplaces" :key="index" :position="getPosition(item)" :clickable="true" @click="toggleInfo(item, key)" icon="http://maps.google.com/mapfiles/ms/icons/green-dot.png" />
      <gmap-marker v-if="bookingid !== ''" v-for="(item, key) in userplaces1" :key="index" :position="getPosition(item)" :clickable="true" @click="toggleInfo(item, key)" icon="http://maps.google.com/mapfiles/ms/icons/red-dot.png" />
      
    </gmap-map>
  </div>

              </div>
              <!-- /.card-body -->
            </div>
</div>
</div>
</div>

</template>

<script>

export default {
  name: 'DriverMap',
  data () {
    return {
      // default to Montreal to keep it simple
      // change this to whatever makes sense
      bookingid: '',
      center: { lat: 10.021512, lng: 77.457939 },
      markers: [],
      places: [],
      userplaces: [],
      userplaces1: [],
      listDriver: [],
      listUser: [],
      coordinates: {
        0: {
          drivername: 'karthi',
          drivermobile: '123',
          lat: '10.021512',
          lng: '77.482660'
        },
        1: {
          drivername: 'venki',
          drivermobile: '1235',
          lat: '10.007016',
          lng: '77.487680'
        }
      },
      currentPlace: null,
      infoPosition: null,
      infoContent: null,
      infoOpened: false,
      infoCurrentKey: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      }
    }
  },
  created: function () {
    this.FetchItem()
  },
  mounted () {
    this.geolocate()
  },
  methods: {
    FetchItem () {
      let uri1 = this.$hostName + "/api/driver/viewDriver?range=''"
      this.axios.get(uri1).then((response) => {
        this.listDriver = response.data
        let mark = []
        let nav = []
        for (var i = 0; i < this.listDriver.length; i++) {
          nav[i] = {
            drivername: this.listDriver[i].driver_name + '(' + this.listDriver[i].driver_id + ')',
            drivermobile: this.listDriver[i].driver_phone,
            lat: this.listDriver[i].latitude,
            lng: this.listDriver[i].longitude
          }
          let marker1 = {
            lat: this.listDriver[i].latitude,
            lng: this.listDriver[i].longitude
          }
          mark.push({ position: marker1 })
        }
        this.markers = mark
        this.places = nav
      })
      if (this.bookingid !== '') {
        uri1 = this.$hostName + '/api/driver/noassignbooking?bookingid=' + this.bookingid
      } else {
        uri1 = this.$hostName + '/api/driver/noassignbooking'
      }

      this.axios.get(uri1).then((response) => {
        console.log(response.data)
        this.listUser = response.data
        let nav = []
        let nav1 = []
        for (var i = 0; i < this.listUser.length; i++) {
          nav[i] = {
            drivername: this.listUser[i].Username + '(' + this.listUser[i].BookingId + ')',
            drivermobile: this.listUser[i].Mobileno,
            lat: this.listUser[i].FromLatitude,
            lng: this.listUser[i].FromLongitude
          }
          nav1[i] = {
            drivername: this.listUser[i].Username + '(' + this.listUser[i].BookingId + ')',
            drivermobile: this.listUser[i].Mobileno,
            lat: this.listUser[i].ToLatitude,
            lng: this.listUser[i].ToLongitude
          }
        }
        this.userplaces = nav
        if (this.bookingid !== '') {
          this.userplaces1 = nav1
        }
      })
    },
    // receives a place object via the autocomplete component
    setPlace (place) {
      this.currentPlace = place
    },
    addMarker () {
      this.FetchItem()
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        }
        // this.markers.push({ position: marker })
        // this.places.push(this.currentPlace)
        this.center = marker
        this.currentPlace = null
      }
    },
    toggleInfo: function (marker, key) {
      this.infoPosition = this.getPosition(marker)
      this.infoContent = marker.drivername + '\n' + marker.drivermobile
      if (this.infoCurrentKey === key) {
        this.infoOpened = !this.infoOpened
      } else {
        this.infoOpened = true
        this.infoCurrentKey = key
      }
    },
    getPosition: function (marker) {
      return {
        lat: parseFloat(marker.lat),
        lng: parseFloat(marker.lng)
      }
    },
    geolocate: function () {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3
{
  margin-top:10px;
}
.content-header
{
  margin:-20px;
}
.col-md-12
{
  margin-left:-20px;
  width:104%
}
.card-body
{
 margin-top: 0px;
 margin-bottom: -10px; 
}
.gm-style-iw-d
{
  white-space: pre-line;
}
</style>



