// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import './lib/css'
import './lib/script'
import './lib/global'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
// import '../node_modules/jquery/dist/jquery.dataTables.css'
// import '../node_modules/jquery/dist/jquery.dataTables.js'

import Vue from 'vue'
import $ from 'jquery'
import App from './App'
import Login from './Login'
import router from './router'
import EventBus from './lib/eventBus.js'
import VueAxios from 'vue-axios'
import VueCookie from 'vue-cookie'
import axios from 'axios'
import { ClientTable } from 'vue-tables-2'
import * as VueGoogleMaps from 'vue2-google-maps'
import { BootstrapVue } from 'bootstrap-vue'
// import store from './vuex/store'

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyDZYpk5JAy91rb9XO86-HIGjXJjfiEBxg8',
    libraries: 'places'
  }
})

Vue.use(BootstrapVue)
Vue.use($)
Vue.use(router)
Vue.use(VueCookie)
Vue.use(ClientTable)

Vue.use(VueAxios, axios)

Vue.prototype.$bus = EventBus
// Vue.prototype.$http = axios

/* eslint-disable no-new */
new Vue({
  el: '#app',
  mode: 'history',
  base: '/Admin',
  router,
  template: (VueCookie.get('isAuth') !== null && VueCookie.get('isAuth') === '1') ? '<App/>' : '<Login />',
  components: {
    App,
    Login
  }
})



