<template>
  <div id="app">
    <div class="container">
    <div class="row">
            <h2 class="headertext"> Welcome to DO'N'KEYCARGO</h2>
        <div class="form_bg">
            <form v-on:submit.prevent="addItem" method="post">
                 <h2 class="text-center">Login Page</h2>
                <br/>
                <div class="form-group">
                    <input type="text" class="form-control" id="userid" placeholder="Mobile No." v-model="username">
                </div>
                <div class="form-group">
                    <input type="password" class="form-control" id="pwd" placeholder="Password" v-model="password">
                </div>
                <div class="align-center">
                    <button type="submit" class="btn btn-default" id="login">Login</button>
                </div>
            </form>
        </div>
    </div>
</div>
  </div>
</template>

<script>

export default {
  name: 'app',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  created () {
    if (window.location.pathname) {
      let url = '/admin'
      if (window.location.pathname === url) {
        this.hidevw = false
      }
    }
  },
  methods: {
    addItem () {
      let items = {}
      let uri = this.$hostName + '/common/EmployeeValidate'
      items.username = this.username
      items.password = this.password
      this.axios.post(uri, items).then((response) => {
        console.log(response)
        if (response.data.cnt > 0) {
          this.$cookie.set('isAuth', 1, { expires: '8h' })
          if (response.data.EmployeeType === 'Admin') {
            this.$cookie.set('isEmployee', true, { expires: '8h' })
          } else {
            this.$cookie.set('isEmployee', false, { expires: '8h' })
          }
          this.$router.go({name: 'Home'})
        } else {
          alert('Username and Password not exist..!!')
        }
      })
    }
  }
}
</script>

<style>
body {
    background:midnightblue;
}
.form_bg {
    background-color:#eee;
    color:#666;
    padding:20px;
    border-radius:10px;
    position: absolute;
    border:1px solid #fff;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 320px;
    height: 280px;
}
.align-center {
    text-align:center;
}
.headertext {
  color: #eee;
  text-align: center;
  padding-top: 8%;
}
.err {
  font-size: small;
  text-align: right;
  padding-top: 5px;
}
</style>



